import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ILoginScreen } from '../models/login.model';

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  private screenSource = new BehaviorSubject<ILoginScreen>('Login');
  currentScreen = this.screenSource.asObservable();

  constructor() {}

  changeScreen(screen: ILoginScreen) {
    this.screenSource.next(screen);
  }
}
